<template>
    <div class="container">
        <div class="row">
            <div class="col-xs-12">
                <v-breadnav>资讯详情</v-breadnav>
            </div>
        </div>
        <div class="row">
            <div class="col-xs-12">
                <div class="information-desc" v-html="descUrl"></div>
            </div>
        </div>
    </div>
</template>

<script>
import vBreadnav from "@/components/pc/breadNav"
export default {
    data:function(){
        return{
            descUrl:this.$route.params.url
        }
    },
    components:{
        vBreadnav
    },
    methods:{
        getiframeHeight:function(){
            let height = $("#desc-ifame").contents().find("body").height();
            console.log(height);
        }
    },
    mounted:function(){
        setTimeout(function(){
            this.getiframeHeight();
        },200)
        
    }
}
</script>



